<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" width="55">
			</el-table-column>
			<el-table-column label="" type="index" width="100">
			</el-table-column>
			<el-table-column prop="name" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="depart" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="uestype" label="" width="120" sortable>
			</el-table-column>
			<el-table-column prop="addr" label="" min-width="180" sortable>
			</el-table-column>
			<el-table-column label="" width="150">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next"  :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.sex"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.age" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="addForm.sex"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="addForm.depart" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					name: '',
					sex: '',
					depart: '',
					uestype: '',
					addr: ''
				},

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					name: '',
					sex: '',
					depart: '',
					uestype: '',
					addr: ''
				}

			}
		},
		methods: {
			//
			// formatSex: function (row, column) {
			// 	return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			// },
			// handleCurrentChange(val) {
			// 	this.page = val;
			// 	this.getUsers();
			// },
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();
				this.total=3;
				this.users = [
					 {
					  id:1,
					  name:'',
					  depart:"p30",
					  uestype:'5999',
					  // sex:1,
					  addr:','
					 },
					 {
					  id:2,
					  name:'',
					  depart:'iphoneX',
					  uestype:'9999',
					  // sex:0,
					  addr:','
					 },
					 {
					  id:3,
					  name:'',
					  depart:"mate20",
					  uestype:'4566',
					  // sex:0,
					  addr:','
					 },
					];
				// console.log(this.users);
				this.listLoading = false;
				
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					for(let i=0;i<this.users.length;i++){
						if(para.id==this.users[i].id){
							this.users.splice(i,1);
							break;
						}
					};
					this.$message({
						message: '',
						type: 'success'
					});
					this.listLoading = false;
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					// sex: -1,
					depart: '',
					birth: '',
					addr: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							// console.log(para);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							for(let i=0;i<this.users.length;i++){
								if(para.id==this.users[i].id){
									this.users.splice(i,1);
									this.users.push(para);
									break;
								}
							};
							this.$message({
								message: '',
								type: 'success'
							});
							this.$refs['editForm'].resetFields();
							this.editFormVisible = false;
							this.editLoading = false;
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							
							// console.log(para);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							this.users.push(para);
							this.$message({
								message:""
							});
							this.addFormVisible = false;
							this.addLoading = false;
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					for(let i=0;i<ids.length;i++){
						for(let j=0;j<this.users.length;j++){
							if(ids[i]==this.users[j].id){
								this.users.splice(j,1);
							}
						};
					}
					this.$message({
						message: '',
						type: 'success'
					});
					this.listLoading = false;
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>

</style>


