<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<!-- <el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="getUsers"></el-button>
				</el-form-item> -->
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="userDevice" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column label="" type="index" width="100">
			</el-table-column>
			<el-table-column prop="devicePid" label="" width="140" sortable>
			</el-table-column>
			<el-table-column prop="deviceName" label="" width="140" sortable>
			</el-table-column>
			<el-table-column prop="deviceType" label="" width="140"  sortable>
			</el-table-column>
			<el-table-column prop="mobile" label="" width="140"  sortable>
			</el-table-column>
			<el-table-column prop="contractNo" label="" width="140"  sortable>
			</el-table-column>
			<el-table-column prop="createdTime" label="" width="160" sortable>
			</el-table-column>
			<!-- <el-table-column prop="addr" label="" min-width="180" sortable>
			</el-table-column> -->
			<el-table-column label="" width="300">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button> 
				<!-- 	<el-button size="small" @click="addSeeBtn(scope.$index, scope.row)"></el-button>
					<el-button size="small" @click="addDeviceBtn(scope.$index, scope.row)"></el-button> -->
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="">
					<el-input v-model="editForm.devicePid "></el-input>
				</el-form-item>
				<el-form-item label="" prop="deviceName ">
					<el-input v-model="editForm.deviceName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="deviceName ">
					<el-input v-model="editForm.deviceType" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.mobile "></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="editForm.contractNo "></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
				<el-form-item label="" prop="devicePid">
					<el-input v-model="addForm.devicePid" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="deviceName">
					<el-input v-model="addForm.deviceName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="deviceType">
					<el-input v-model="addForm.deviceType" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="addForm.mobile" ></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="addForm.contractNo" ></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
		<!--  -->

		
		<!--  -->
		<el-dialog title="" v-model="addFormDevice" :close-on-click-modal="false">
			<el-form :model="addDevice" label-width="80px" :rules="addDeviceRules" ref="addDevice">
				<el-form-item label="" prop="name">
					<el-input v-model="addDevice.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="">
					<el-radio-group v-model="addDevice.type">
						<el-radio class="radio" :label="0"></el-radio>
						<!-- <el-radio class="radio" :label="1"></el-radio>
						<el-radio class="radio" :label="2"></el-radio> -->
					</el-radio-group>
				</el-form-item>
				<el-form-item label="">
					<el-input v-model="addDevice.moudl" ></el-input>
				</el-form-item>
		
				<!-- <el-form-item label="">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item> -->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormDevice = false"></el-button>
				<el-button type="primary" @click.native="addDevSubmit" :loading="addDevLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getDevList,NewAddDevice,getDevice,editDevice} from '../../api/api';

	export default {
		created() {
		        console.log(this.$route.query.id);
		    },

		data() {
			return {
				filters: {
					name: ''
				},
				userId:'',
				userDevice: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					userName: '',
					userType: -1,
					mobile: ''
				},
				//
				addFormVisible: false,
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
					
				},
				//
				addForm: {
					deviceName : '',
					deviceType: '',
					userId: ''
					
				},
				//
				addFormSee:false,  
				addSeeLoading:false,
				addSeeRules:{
					
				},
				//
				addSee: {
					name: '',
					type: -1,
					moudl: ''
					
				},
				//
				addFormDevice:false,  
				addDevLoading:false,
				addDeviceRules:{
					
				},
				//
				addDevice: {
					name: '',
					type: -1,
					moudl: ''
					
				},

			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getUserDevice();
			},
			//
			getUserDevice() {
				let para = {
					userId:this.userId,
					pageNum: this.page,
					pageSize :10
				};
				this.loading = true;
				//NProgress.start();
				getDevList(para).then((res) => {
					this.userDevice = res.data.data;
					this.loading = false;
					//NProgress.done();
				});
				
			},
			//
			// handleDel: function (index, row) {
			// 	this.$confirm('?', '', {
			// 		type: 'warning'
			// 	}).then(() => {
			// 		this.listLoading = true;
			// 		//NProgress.start();
			// 		let para = { id: row.uuid };
			// 		removeUser(para).then((res) => {
						
			// 			this.listLoading = false;
			// 			//NProgress.done();
			// 			this.$message({
			// 				message: '',
			// 				type: 'success'
			// 			});
			// 			this.getUsers();
			// 		});
			// 		this.listLoading = false;
			// 	}).catch(() => {

			// 	});
			// },
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					userName: '',
					userType: -1,
					mobile: ''
				};
			},
			//
			addSeeBtn:function () {
				let uuid=row.uuid;
				console.log(uuid);
			},
			//
			addDeviceBtn:function () {
				this.addFormDevice = true;
				this.addDevice = {
					name: '',
					type: -1,
					moudl: ''
					
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
						
							//para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editDevice(para).then((res) => {
								this.editLoading = false;
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUserDevice();
							});
						});
					}
				});
			},
			
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							let para = Object.assign({}, this.addForm);
							para.userId=this.userId;
							//para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							NewAddDevice(para).then((res) => {
								console.log(res);
								this.addLoading = false;
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUserDevice();
							});
						});
					}
				});
			},
			
			
			// 
			addDevSubmit: function () {
				this.$refs.addDevice.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addDevLoading = true;
							let par = Object.assign({}, this.addDevice);
							console.log(par);
						});
					}
				});
			},
			
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					for(let i=0;i<ids.length;i++){
						for(let j=0;j<this.users.length;j++){
							if(ids[i]==this.users[j].id){
								this.users.splice(j,1);
							}
						};
					}
					this.$message({
						message: '',
						type: 'success'
					});
					this.listLoading = false;
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.userId=this.$route.query.id;
			this.getUserDevice();
		}
	}

</script>

<style scoped>

</style>


