import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import User from './views/nav1/user.vue'
import Customer from './views/nav1/customer.vue'
import Department from './views/nav1/department.vue'
import UserDevice from './views/nav1/userDevice.vue'
import list from './views/nav2/list.vue'
import task from './views/nav3/task.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
	{
	    path: '/',
	    component: Home,
	    name: '',
	    iconCls: 'fa fa-bar-chart',
	    children: [
	        { path: '/echarts', component: echarts, name: '' }
	    ]
	},
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',//class
        children: [
            { path: '/main', component: Main, name: '', hidden: true },
            { path: '/user', component: User, name: '' },
			{ path: '/customer', component: Customer, name: '' },
			{ path: '/department', component: Department, name: '' },
			{ path: '/userDevice', component: UserDevice, name: '',hidden: true}
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-wrench',
        children: [
			{ path: '/list', component: list, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',
        // leaf: true,       //
        children: [
            { path: '/task', component: task, name: '3' }
        ]
    },
    
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


